<template>
  <div id="tablet">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="success" size="small" style="margin-right: 10px" class="btn-import" @click.stop="addActive = true;$refs.extracTableMap.initCom()"><i class="iconfont icon-xuanzewenjian"></i>自动提取</Button>
      <Button type="success" size="small" class="btn-export" style="padding: 2px 7px 1.8px">
        <Upload
          :show-upload-list="false"
          :format="['.xls']"
          action="#">
          <i class="iconfont icon-upload"></i>
          <span>识别导入</span>
        </Upload>
      </Button>
    </QueryBox>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <div class="extract_box" :class="{addActive:addActive}">
      <extracTableMap ref="extracTableMap" @signOut="signOut"></extracTableMap>
    </div>
  </div>
</template>

<script>
import QueryBox from './extracTable_query'
import extracTableMap from './extracTable_map'
import {
  // 表格列表
  getSurveyTableList
} from '@/api/dataCollection'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 312,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '影像地图',
          align: 'center',
          minWidth: 200,
          key: 'srld_code'
        },
        {
          title: '人类活动信息',
          align: 'center',
          width: 120,
          key: 'srld_code',
          render: (h, params) => {
            return h('i', {
              class: 'iconfont icon-yigouxuan',
              style: {
                marginRight: '5px'
              }
            })
          }
        },
        {
          title: '来源',
          align: 'center',
          minWidth: 150,
          key: 'srld_code'
        },
        {
          title: '最后更正时间',
          align: 'center',
          width: 150,
          key: 'survey_time'
        },
        {
          title: '操作',
          key: 'address',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    console.log('标绘修正')
                  }
                }
              }, '标绘修正')
            ])
          }
        }
      ],
      data1: [],
      /* 查询框查询条件 */
      obj: {},
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 312) / 48),
      /* 加载 */
      loading: false,
      /* 提取信息框状态 */
      addActive: false
    }
  },
  components: {
    QueryBox,
    // 信息提取地图
    extracTableMap
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 312
      this.pageSize = Math.floor((window.innerHeight - 312) / 48)
      this.getTableList()
    }, false)
  },
  watch: {
    obj () {
      this.getTableList()
    }
  },
  methods: {
    /* 接受查询框查询条件 */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getSurveyTableList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          param: this.obj.param,
          redlineName: this.obj.srldName,
          tableName: this.obj.param
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 信息提取地图页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-upload {
        font-size: 12px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
