<template>
  <div class="map-box">
    <div id="viewDiv" ref="viewDiv"></div>
    <!-- 保存、取消-->
    <p class="btns">
      <Button type="success" size="small" class="btn-save mr15"><i class="iconfont icon-Save"></i>保存</Button>
      <Button type="primary" size="small" class="btn-commit mr15"><i class="iconfont icon-tijiao"></i>保存并提交</Button>
      <Button type="error" size="small" @click="$emit('signOut',false)" class="btn-cancel"><i class="iconfont icon-del"></i>取消</Button>
    </p>
    <!-- 地图的图层控制 -->
    <ButtonGroup id="mapLayers">
      <Poptip placement="right">
        <Button>
          <i class="iconfont icon-tuceng"></i>
        </Button>
        <div slot="content" class="pop-content">
          <ul>
            <li>
              <Checkbox label="layer1"></Checkbox>业务图层</li>
            <li>
              <p style="cursor: pointer;" @click="layerCon = !layerCon;$emit('layerConClick')">
                <Checkbox label="layer12"></Checkbox>基年数据图层
                <Icon :type="layerCon ? 'md-arrow-dropdown-circle' : 'md-arrow-dropright-circle'" />
              </p>
              <draggable class="fraggableBox" v-if="layerCon">
                <p>
                  <Checkbox label="layer21"></Checkbox>图层1
                </p>
                <p>
                  <Checkbox label="layer22"></Checkbox>图层2
                </p>
              </draggable>
            </li>
          </ul>
        </div>
      </Poptip>
    </ButtonGroup>
    <!-- 操作工具条 -->
    <div class="toolsBtns">
      <button title="选择">
        <i class="iconfont">&#xe623;</i>
      </button>
      <button title="剪切">
        <i class="iconfont">&#xe601;</i>
      </button>
      <button title="新建">
        <i class="iconfont">&#xe64b;</i>
      </button>
      <button title="融合">
        <i class="iconfont f25">&#xe713;</i>
      </button>
      <button title="交集">
        <i class="iconfont">&#xe6e8;</i>
      </button>
      <button title="打散">
        <i class="iconfont">&#xe616;</i>
      </button>
      <button title="属性操作">
        <i class="iconfont">&#xe727;</i>
      </button>
      <button title="删除">
        <i class="iconfont">&#xe635;</i>
      </button>
    </div>
  </div>
</template>
<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie } from '@/utils/layers'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'
import draggable from 'vuedraggable'
export default {
  name: 'extracTableMap',
  data () {
    return {
      map: {}, // 地图实例
      view: {}, // 地图视图实例
      layerCon: false, // 控制图层控制器开关
      type: '' // 正在使用的测量工具标识
    }
  },
  computed: {},
  props: {},
  components: {
    draggable
  },
  watch: {},
  mounted () {
    // 加载地图
    this.createMap()
  },
  methods: {
    /**
     * @Description:初始化地图的函数
     * @author 张淼
     * @date 2019/5/5
     */
    async createMap () {
      // 加载arcgis地图的依赖
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen
      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })
      // 省界
      let layerProvince = await shengjie().then(res => { return res })
      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, layerProvince, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, layerProvince, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })
      this.map = new Map({
        basemap: customBasemap1
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      // 图层数据查看
      this.layerRedLine = new FeatureLayer({
        url: config.serverUrl + '/arcgis/rest/services/qhhx/hongxian/MapServer',
        opacity: 0.7,
        outFields: ['*'],
        popupTemplate: {
          title: '{name}',
          content: [
            {
              type: 'fields',
              fieldInfos: [{
                fieldName: '行政区',
                label: '所属地区'
              }, {
                fieldName: '面积',
                label: '面积（平方米）'
              }]
            }
          ]
        }
      })
      // 添加图层数据
      this.map.layers.add(this.layerRedLine)
      // 增加底图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap
      })

      this.view.ui.add(basemapToggle, 'top-right')
      // 添加全屏
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 添加图层控制工具框
      this.view.ui.add(document.getElementById('mapLayers'), 'top-left')
    },
    /**
     * 初始化组件
     * */
    initCom (id) {
      this.map.remove(this.insercLayer)
      this.view.graphics.removeAll()
    }
  }
}
</script>
<style scoped lang="scss">
  .map-box{
    width: 100%;
    height: 100%;
    padding: 15px;
    position: relative;
    /* 地图框样式 */
    #viewDiv{
      width: 100%;
      height: 100%;
    }
    .btns {
      width: 100%;
      text-align: center;
      position: absolute;
      right: 20px;
      bottom: 30px;
      .ivu-btn {
        border-radius: 0;
        i {
          margin-right: 5px;
        }
        &.btn-save {
          background-color: #4aa432;
          border-color: #4aa432;
          &:hover, &:focus {
            background-color: #388524;
            border-color: #388524;
          }
        }
        &.btn-commit {
          background-color: #597fd5;
          border-color: #597fd5;
          &:hover, &:focus {
            background-color: #486ec4;
            border-color: #486ec4;
          }
        }
        &.btn-cancel {
          background-color: #ff3b3b;
          border-color: #ff3b3b;
          &:hover, &:focus {
            background-color: #e22727;
            border-color: #e22727;
          }
        }
      }
    }
    .mr15 {
      margin-right: 15px;
    }
    #mapLayers {
      .ivu-btn {
        padding: 0px 5px 0px;
      }
      .icon-tuceng {
        font-size: 20px;
      }
    }
    .pop-content {
      text-align: left;
      li {
        line-height: 35px;
        border-top: 1px solid #ccc;
        &:first-child {
          border: none;
        }
        i {
          font-size: 16px;
          margin-left: 30px;
        }
        .fraggableBox {
          &>p {
            padding-left: 22px;
            line-height: 20px;
          }
        }
      }
    }
    .toolsBtns{
      background: #fff;
      position: absolute;
      top: 30px;
      right: 115px;
      display: flex;
      justify-content: center;
      align-items: center;
      button {
        width: 35px;
        height: 35px;
        border: 0;
        background: #fff;
        cursor: pointer;
        outline: none;
        i{
          font-size: 20px;
          color: #4d4b4b;
        }
        &:hover {
          i {
            color: #1f7efe;
          }
        }
        .f25 {
          font-size: 25px;
        }
      }
      .active{
        background-color: #4C4C4C;
        color: #fff;
      }
      .disable{
        background-color: #fff;
        color: #d1d1d1;
      }
    }
  }
</style>
